 * @Description: 提现列表
 * @Author: your name
 * @Date: 2019-08-13 10:15:18
 * @LastEditTime: 2019-09-10 19:38:26
 * @LastEditors: Please set LastEditors
 -->
<template>
  <div>
    <!-- 筛选条件 -->
    <el-card class="filter-container"
             shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float: right"
                   @click="searchList"
                   type="primary"
                   size="small">
          查询
        </el-button>
        <el-button style="float: right;margin-right: 15px"
                   @click="reset"
                   size="small">
          重置
        </el-button>
      </div>
      <div style="margin-top: 30px">
        <el-form :inline="true"
                 :model="list"
                 size="small"
                 label-width="140px">
          <el-form-item label="微信昵称：">
            <el-input v-model="list.nickName"
                      clearable></el-input>
          </el-form-item>
          <el-form-item label="手机号：">
            <el-input v-model.trim="list.cellPhone"
                      clearable></el-input>
          </el-form-item>
          <el-form-item label="提现单号：">
            <el-input v-model.trim="list.withdrawNo"
                      clearable></el-input>
          </el-form-item>
          <el-form-item label="提现到：">
            <el-select v-model="list.channel"
                       clearable
                       placeholder="默认选择全部">
              <el-option v-for="item in channelList"
                         :key="item.value"
                         :label="item.label"
                         :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="提现时间：">
            <el-date-picker v-model="list.time"
                            type="daterange"
                            value-format="yyyy-MM-dd"
                            :picker-options="pickerOptions"
                            range-separator="至"
                            start-placeholder="开始日期"
                            end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <!-- 数据列表 -->
    <el-card class="operate-container operateCard"
             shadow="never">
      <i class="el-icon-tickets"></i>
      <span>数据列表</span>
      <!-- <el-button type="primary" style="float:right;margin-top:-10px;margin-right: 10px" @click="exportExcel">导出Excel</el-button> -->
      <!-- <el-button type="text"
                 style="float:right;margin-top:-10px;margin-right: 10px"
                 @click="poundageShow">手续费设置</el-button> -->
    </el-card>
    <div class="table-container">
      <el-table :data="tableList"
                style="width: 100%"
                ref="multipleTable"
                v-loading="loading"
                border>
        <el-table-column type="index"
                         width="50">
        </el-table-column>
        <el-table-column label="提现单号"
                         width="190"
                         align="center">
          <template slot-scope="scope">
            <span>{{scope.row.withdrawNo}}</span><i v-if="scope.row.dayNum > 3"
               class="el-icon-warning"
               style="color:red"></i>
          </template>
        </el-table-column>
        <el-table-column label="微信昵称"
                         width="100"
                         prop="nickName"
                         align="center">
        </el-table-column>
        <el-table-column label="提现金额"
                         width="130"
                         align="center">
          <template slot-scope="scope">￥{{scope.row.withdrawAmount}}</template>
        </el-table-column>
        <el-table-column label="手机号"
                         width="150"
                         prop="cellPhone"
                         align="center">
        </el-table-column>
        <el-table-column label="提现到"
                         width="150"
                         align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.type == 1">
              微信零钱
            </span>
            <span v-if="scope.row.type == 2">
              银行卡
            </span>
          </template>
        </el-table-column>
        <el-table-column label="手续费"
                         width="130"
                         prop="withdrawCommission"
                         align="center">
          <template slot-scope="scope">￥{{scope.row.withdrawCommission}}</template>
        </el-table-column>
        <el-table-column label="提现时间"
                         width="180"
                         prop="createTime"
                         align="center">
        </el-table-column>
        <el-table-column label="状态"
                         width="150"
                         align="center">
          <template slot-scope="scope">
            <el-tag type="primary"
                    v-if="scope.row.auditType == 1">待审核</el-tag>
            <el-tag type="danger"
                    v-if="scope.row.auditType == 5">已驳回</el-tag>
            <el-tag type="success"
                    v-if="scope.row.auditType == 3">银行处理中</el-tag>
            <el-tag type="success"
                    v-if="scope.row.auditType == 2">提现成功</el-tag>
            <el-tag type="danger"
                    v-if="scope.row.auditType == 4">提现失败</el-tag>
          </template>
        </el-table-column>
        <el-table-column label="操作"
                         width="200"
                         align="center">
          <template slot-scope="scope">
            <el-button type="text"
                       v-if="scope.row.auditType == 1"
                       @click="showDetails(scope.$index,scope.row)">审核</el-button>
            <el-button v-if="scope.row.auditType != 1"
                       type="text"
                       @click="showDetails(scope.$index,scope.row)">查看</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="flex-row-end"
           style="margin-top:20px">
        <!-- 页码 -->
        <el-pagination background
                       style="margin-top: 10px;margin-left:20px"
                       @size-change="handleSizeChange"
                       @current-change="pageChange"
                       layout="total,sizes,prev,pager,next,jumper"
                       :page-size="page_size"
                       :page-sizes="[10, 30, 50]"
                       :current-page="pageNum"
                       :total="total">
        </el-pagination>
      </div>
    </div>

  </div>
</template>
<script>
import { getUserWithdrawList, auditUserWithdraw } from '@/api/settleManagement/withdrawal'
export default {
  props: ["status", "shop", "user"],
  data () {
    return {
      list: {
        nickName: '',
        cellPhone: '',
        withdrawNo: '',
        time: [],
        type: '',
        channel: ''
      },
      loading: false,
      tableList: [],
      pageNum: 1,
      page_size: 10,
      total: 0,
      id: '',
      channelList: [{
        value: '0',
        label: '全部'
      }, {
        value: '1',
        label: '微信零钱'
      }, {
        value: '2',
        label: '银行卡'
      }],
      pickerOptions: {
        disabledDate (time) {
          return time.getTime() > Date.now()
        },
        shortcuts: [
          {
            text: '今天',
            onClick (picker) {
              const end = new Date();
              end.setTime(end.getTime());
              end.setHours(end.getHours());
              end.setMinutes(end.getMinutes());
              end.setSeconds(end.getSeconds());
              const start = new Date();
              start.setTime(start.getTime());
              start.setHours(0); start.setMinutes(0); start.setSeconds(0);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '昨天',
            onClick (picker) {
              const end = new Date();
              end.setTime(end.getTime() - 3600 * 1000 * 24);
              end.setHours(23);
              end.setMinutes(59);
              end.setSeconds(59);
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24);
              start.setHours(0); start.setMinutes(0); start.setSeconds(0);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: '最近一周',
            onClick (picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一个月',
            onClick (picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          },
        ]
      },
    }
  },
  created () {
    this.searchList()
  },
  mounted () {

  },
  methods: {
    //查询数据列表
    searchList () {
      this.loading = true
      getUserWithdrawList({
        shopId: this.shop.shopId,
        tabType: this.status,
        pageNum: this.pageNum,
        pageSize: this.page_size,
        nickName: this.list.nickName,
        cellPhone: this.list.phoneNumber,
        withdrawNo: this.list.withdrawNo,
        type: this.list.channel,      //提现渠道   1：微信零钱；2：银行卡
        createTimeStart: this.list.time == null ? '' : this.list.time[0],
        createTimeEnd: this.list.time == null ? '' : this.list.time[1]
      }).then(res => {
        if (res.code == 200) {
          this.tableList = res.data.list
          this.total = res.data.total
          this.loading = false
        } else {
          this.$notify.error({
            title: '失败',
            message: res.message
          });
        }
      })
    },

    //查看详情
    showDetails (index, row) {
      this.$router.push({
        path: "/settleManagement/withdrawal/withdrawalDetails",
        query: {
          id: row.id
        }
      })
    },

    //重置
    reset () {
      this.list = {
        nickName: '',
        cellPhone: '',
        withdrawNo: '',
        time: [],
        type: '',
        channel: ''
      }
      this.page_size = 1
      this.searchList()
    },
    //修改每页页数
    handleSizeChange (page) {
      this.page_size = page
      this.searchList()
    },
    //点击跳转页码
    pageChange (page) {
      this.pageNum = page
      this.searchList()
    },
    //选择按钮
    handleSelectionChange (val) {
      this.multipleSelection = val;
    },
  },
  computed: {
    startTime () {
      return this.status;
    },
  },
  watch: {
    startTime (newValue, oldValue) {
      this.searchList()
      return newValue;
    },
  }
}
</script>

<style lang="scss" scoped>
.dia-radio /deep/ .el-radio-group {
  border: none;
}
.item-contain-red {
  color: red;
  margin: 0 30px 0 36px;
  line-height: 36px;
}
.operate-container {
  margin-top: 10px;
}
.table-container {
  margin-top: 10px;
}
.btn-add {
  float: left;
  margin-left: 10px;
}
img {
  width: 100%;
}
</style>
